@font-face {
  font-family: "JetbrainsMono";
  src: url("../fonts/JetbrainsMono.woff2") format("woff2");
}

$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: "JetbrainsMono",-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";



body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
}
